class OTF_RealEstate_Tabs {
    constructor() {
        if ($('body').hasClass('property-style-3')) {
            this.setupNavTab();
        } else if ($('body').hasClass('property-style-4')) {
            this.setupNavScroll();
        }
    }

    setupNavScroll() {
        let lastId,
            topMenu       = $(".single-property-tabs"),
            topMenuHeight = topMenu.outerHeight() + 50,
            menuItems     = topMenu.find("a"),
            scrollItems   = menuItems.map(function () {
                let item = $($(this).attr("href"));
                if (item.length) {
                    return item;
                }
            });

        menuItems.click(function (e) {
            let href      = $(this).attr("href"),
                offsetTop = href === "#" ? 0 : $(href).offset().top - topMenuHeight + 1;
            $('html, body').stop().animate({
                scrollTop: offsetTop
            }, 300);
            e.preventDefault();
        });

        $(window).scroll(function () {
            let fromTop = $(this).scrollTop() + topMenuHeight;
            let cur = scrollItems.map(function () {
                if ($(this).offset().top < fromTop)
                    return this;
            });
            cur     = cur[cur.length - 1];
            let id  = cur && cur.length ? cur[0].id : "";

            if (lastId !== id) {
                lastId = id;
                menuItems
                    .parent().removeClass("active")
                    .end().filter("[href='#" + id + "']").parent().addClass("active");
            }
        });
    }

    setupNavTab() {
        let $container = $('.single-property-tabs');
        $container.find('a').on('click', (event) => {
            event.preventDefault();

            let $a = $(event.currentTarget);
            $container.find('li').removeClass('active');
            $a.parent().addClass('active');

            this.setupTabContent($a);

        })
    }

    setupTabContent($element) {
        let selector = $element.attr('href');
        if (selector) {
            let $selector = $(selector);
            $selector.closest('.single-after-summary').children('div').removeClass('show');
            $selector.addClass('show');
        }
    }
}
new OTF_RealEstate_Tabs();